				"html": "<p>When it comes to designing your wedding, the choice really is yours.\nThere are a variety of different themes and styles that you can choose from, and it is important to ensure that it is perfect for you.</p>\n<p>One theme that we have seen recently and think works beautifully is that of a touch of greenery.\nGreenery wedding themes are an alternative to a standard floral theme. Instead of using brightly colored blooms for decoration, a greenery theme will arrange branches, leaves and palms to have a minimalistic but still beautiful effect.\nWe think that greenery works beautifully for weddings no matter the season, however, you can change the materials you use to match in with when you are getting married.\nWant to try it for yourself? Here are some of the best greenery wedding decoration ideas that we have seen recently.</p>\n<h2>A greenery wedding cake</h2>\n<p>A great way that we have seen to incorporate greenery into your wedding is with the cake. Often the focal point of the wedding reception (or at least the part that plenty of guests look forward to) the wedding cake is a great way to embrace your theme. Why not request that is iced with a simple white or cream buttercream, then delicately decorated with leaves or branches. You could even go one step further and have it displayed on a wooden board.\n<img src=\"http://cordially.co/static/img/Greenery-wedding-cake-ideas.jpg\" alt=\"greenery wedding theme - button holes\"></p>\n<h2>A leafy table runner</h2>\n<p>When you think of table runners for your wedding day you are likely to think of lavish gold shades and silky textures. However, it doesn’t always have to be that way. In fact, you can use your green theme in your table runner with ease. Creating a leafy green runner and place golden, silver or perhaps even glittery tea light holders throughout the branches and you will have a table runner that you will be proud to display.\n<img src=\"http://cordially.co/static/img/Greenery-table-runner.jpg\" alt=\"greenery wedding theme - button holes\"></p>\n<h2>The bouquet and button holes</h2>\n<p>Who says that your wedding bouquet and button holes need to be packed full of flowers. Whilst it may be the norm to opt for big and bold blooms, if you are heading for a greenery theme then you may want to opt for more leaves instead. This can be combined with the same style for the button holes, making for a beautiful look that will fit perfectly with your set style.\n<img src=\"http://cordially.co/static/img/greenery-wedding-boutonnieres.jpg\" alt=\"greenery wedding theme - button holes\"></p>\n<h2>Ivy for your archway</h2>\n<p>Quite a few couples like the idea of saying their vows in an archway, so why not make a plain cream one that little bit more special by adding in some ivy. This will not only offer a beautiful touch of green, but also create a dramatic look that will be perfect for those “I do” pictures.\n<img src=\"http://cordially.co/static/img/greenery-wedding-arch.jpg\" alt=\"greenery wedding theme - button holes\"></p>\n<p>Think that you like the idea of a greenery wedding theme? Want to try out how it would look for yourself? Why not check out our wedding website creator? Perfect for those who want to make a wedding website, we can help you to create a website that not only looks beautiful, but that gives your guests all the information that they need.</p>",
